h1,
.input {
    text-transform: uppercase;
}

h1 {
    font-size: calc(2.5rem + 1.25vw);
    max-width: 100%;
    margin-top: 15%;
    line-height: 1em;
}

.inputContainer {
    max-width: 200px;
}

.input {
    margin-right: -.18em;
    resize: horizontal;
    color: currentColor;
    display: inline-block;
    font: inherit;
    background: transparent;
    outline: none;
    border: none;
    -webkit-transition: width .4s;
    transition: width .4s;
}

.has-indicator {
    position: relative;
    display: inline-block;
}

.has-indicator:before {
    content: '';
    position: absolute;
    top: 100%;  /* position the top  edge of the element at the middle of the parent */
    left: 50%;
    top: 100%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.has-indicator:after {
    content: attr(data-indicator);
    font-size: 14px;
    font-weight: 300;
    position: absolute;
    text-transform: lowercase;
    border-top: 1px solid #000;
    padding-top: 10px;
    line-height: 1;
    width: 100%;
    text-align: center;
    left: 50%;
    top: 95%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.btn {
    border: none;
    margin: 0;
    padding: 0;
    width: auto;
    overflow: visible;

    background: currentColor;
    color: invert(1);
    color: inherit;
    font: inherit;
    -webkit-appearance: none;
    outline: none;
    padding: .5rem 1rem;
    cursor: pointer;
}

.btn:hover {
    background-color: #ccc;
}
.btn span {
    -webkit-filter: invert(100%);
            filter: invert(100%);
}


@font-face {
    font-family: 'Helvetica Neue';
    src: url(/static/media/helveticaneue-boldext-webfont.d011d88d.woff2) format('woff2'),
         url(/static/media/helveticaneue-boldext-webfont.2c943a88.woff) format('woff');
    font-weight: bold;
    font-style: normal;

}

@font-face {
    font-family: 'Helvetica Neue';
    src: url(/static/media/helveticaneue-roman-webfont.934334bc.woff2) format('woff2'),
         url(/static/media/helveticaneue-roman-webfont.f515eabe.woff) format('woff');
    font-weight: normal;
    font-style: normal;

}

*,
*:before,
*:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Helvetica Neue', sans-serif;
    font-weight: 400;
    line-height: 1.5;
    -webkit-font-smoothing: inherit;
    -moz-osx-font-smoothing: inherit;
}

a {
    text-decoration: none;
    color: currentColor;
    border-bottom: 1px solid currentColor;
}

.bg {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
}

.container {
    width: 80%;
    margin: 0 auto;
    padding: 60px 0;
    position: relative;

    display: flex;
    flex-direction: column;
    height: calc(100vh - 20px);
    justify-content: space-between;
}

.content {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-wrap: wrap-reverse;
}

.content button {
    margin-bottom: 5vh;
}

@media (min-width: 490px) {
    .content button {
        margin-bottom: 0;
    }
}

.info {
    max-width: 288px;
}

.hidden {
    display: none;
}
